body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 16px;

  background:linear-gradient(rgba(255,255,255,.9), rgba(255,255,255,.9)), url('data:image/svg+xml;headers=Content-Disposition%3A%20attachment%3B%20filename=pattern.b64;base64,');
}

button{
  outline: none;
  border: none;
}
.center{
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.h2{
  text-transform: uppercase;
  position: relative;
}

.container{
  height: 50vh;
  width: 25vmax;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 20px;
  overflow: hidden;
  box-shadow:  0 0 60px rgba(0,0,0,.1);
}

.container .container-heading{
  width: 100%;
  max-width: 100%;
  height: 75%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #eeebea;
  color: #71462b;
  font-size: 6em;
}

.container .container-display{
  width: 100%;
  height: 25%;
  max-width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.container .container-display button{
  background-color: #b08b6d;
  height: 100%;
  width: 100%;
  max-width: 100%;
  color: #080401;
  font-size: 1.5em;
}
.container .container-display button:first-child{
  background-color: #684027;
  color: white;
}
.container .container-display button:last-child{
  background-color: #322013;
  color: white;
}





